import react, {useState, useEffect} from "react";
import s from "./Footer.module.css";
import mailImg from "../assets/img/mail.svg";
import {db} from "./Messages";
import {connect} from "react-redux";
import {updateCurrentUser} from "../redux/store";
import firebase from "../firebase";

const Footer = (props) => {

    const [inputValue, setInputValue] = useState("");

    useEffect( () => {
        let user = firebase.auth().currentUser;
        if(user) props.updateCurrentUser(user.uid);
        console.log(props.id);
    })
   

    let submitForm = (e, message) => {
        e.preventDefault();
        if(message) {
            db.collection("messages").add({
                message,
                id: props.messageLength,
                userId: props.id,
            }).then( () => {
                setInputValue("")
            } )
        }
    }

    return(
        <div className={s.footer}>
            <form onSubmit={ (e) => submitForm(e, inputValue)} className={s.form}>
                <input value={inputValue} onChange={ (e) => setInputValue(e.target.value) } className={s.input} type="text"/>
                <button className={s.button}>
                    <img src={mailImg} alt=""/>
                </button>
            </form>
        </div>
    )
}

let mapStateToProps = (state) => ({
    messageLength: state.messages.length,
    id: state.currentUser.uid
    }

)
export default connect(mapStateToProps, {updateCurrentUser})(Footer);